html, body {
    width:100%;
    height:100%;
}

.wrapper-1 {
  width: 100%;
  margin-top:4.6rem;
}
.wrapper-2{
    width: 100%;
    margin-top:0;
}
.book-loading{
    display:none;
}
.book-loading img {
  margin-bottom: -7px;
  display: inline-block;
}

.book-header-fixed {
    float:left;
  position:fixed;
  left:0;
  top:0;
  right:0;
  z-index:1100;
  background: #333;
  height:4.5rem;
  overflow: hidden;
}
.book-header-wu{
    background: #333;
    height:4.5rem;
    overflow: hidden;
}
.book-style-header{
	.book-style-pic{
		float: left;
		width:4rem;
		height:4.5rem;
		margin-left:1.5rem;
		padding-top:1rem;
		img{
			display:block;
	  		width:2.5rem;
	    	border-radius: 50%;
		}
	}
	.book-style-name{
		height:4.5rem;
    	line-height:4.5rem;
		float: left;
		width:6.4rem;
		h2{
			width:8rem;
			font-size: 1.6rem;
			color: #fff;
			font-weight: bold;
		}
	}
}


#book-style-container{
	.book-style-filter{
		background: #fafafa;
		border-bottom: 1px solid #ccc;
		padding: 2rem 0 1rem 1.5rem;
		.filter{
			line-height:2.6rem;
			padding-bottom:1rem;
			display:-webkit-box;
			display:-moz-box;
			display:box;
			.filter-paihang{
				h2{
					font-size:1.6rem;
					color: #666;
					font-weight:bold;
					margin-right:10px;
				}
			}
			.paihang-type{
				-webkit-box-flex:1.0;
				-moz-box-flex:1.0;
				box-flex:1.0;
				overflow: hidden;
				li{
					float: left;
					height:2.5rem;padding:0 1rem;
					text-align: center;
					border-radius:0.6rem;
					font-size:1.4rem;
					font-weight: 600;
					letter-spacing:0.1rem;
					color: #fff;
					line-height:2.5rem;
					background: #ccc;
					&.filter-active{
						background: #6cd;
					}
					&.filter-no{
						background: #ccc;
					}
					&:nth-child(2){
						margin: 0 1rem;
					}
				}
			}
		}
	}
  .this-book-list{
		padding:0rem 2rem 1.5rem 1.5rem;
		overflow:hidden;
		.book-list-have{
			.book-name-1{
				padding:1.5rem 0;
				border-bottom: 1px solid #e6e6e6;
				display: -webkit-box;
				display: -moz-box;
				display:box;
				overflow: hidden;
				.book-pic{
					margin-right:20px;
					img{
						display:block;
						width:80px;
						height: auto;
						border-radius:6px;
					}
				}
				.book-detail{
					-webkit-box-flex:1;
					-webkit-box-flex:1;
					box-flex:1;
					margin-top:0px;
					.book-det-h{
						font-size:1.6rem;
						padding:10px 0 12px;
						a{
							color: #333;
							font-weight: bold;
						}
					}
					.detail-1{
						padding-bottom:0.5rem;
						font-size:1.2rem;
					    font-weight: 500;
					    color: #666;
					    b{
							color: #666;
							font-weight: normal;
					    }
					    a{
					    	color: #666;
							font-size:1.2rem;
					    }
					}
					.detail-intro{
						color: #999;
						font-size:1.2rem;
						line-height:1.8rem;
						height:3.6rem;
						overflow:hidden;
						a{

						}
					}
				}
			}
		}
		.book-list-null{
			margin-top:8rem;
			text-align: center;
			.daixu{
				margin-left: -1.7rem;
				img{
					width:3.4rem;
				}
			}
			.yuanquan{
				overflow: hidden;
				margin-bottom:6rem;
				margin-left: 14rem;
				li{
					margin-right:0.8rem;
					float: left;
					img{
						display: block;
						width:0.8rem;
					}
				}
			}
			p{
				display:block;
				height:2rem;
				line-height:2rem;
				font-size:1.4rem;
				color:#bbb;
				&:nth-child(2){
					text-indent: 2rem;
				}
			}
		}
	}
    .book-loading{
		height:4.4rem;
		line-height:4.4rem;
		text-align: center;
		padding-left:20%;
		overflow: hidden;
		a{
			float:left;
			padding-top:0.6rem;
			img{
				display: block;
				width:3rem;
			}
		}
		span{
			float:left;
			padding-left:2rem;
			font-size:1.6rem;
			color: #999;
		}
    }
    .book-null{
		display:none;
    }
}

