.book-style-none {
    float:left;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index:1010;
    height:4.5rem;
    padding:0.7rem 0 0;
    background: #333;
    border-bottom: 1px solid #ccc;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    .selects{
    	-webkit-box-flex: 1;
	    -webkit-flex: 1;
	    flex: 1;
	    width:1px;
	    position: relative;
	    font-weight: 600;
	    letter-spacing:0.1rem;
	    outline: none;
	    height:2.9rem;
	    margin: 0 0.7rem;
	    appearance: none;
	    -webkit-appearance: none;
	    border: 1px solid #6cd;
	    border-radius:0.6rem;
	    color: #6cd;
	    background: #333;
	    background: url("/StaticResources/public_html/wings/_resources/img/wx/laikan/img/select_jianjiao.png") no-repeat 90% center;
	    background-size:1.1rem 0.8rem;
	    .select-text{
			width: 100%;
			height: 2.9rem;
			line-height:2.9rem;
			padding-left:0.4rem;
			font-size:1.4rem;
	    }
	    ul{
	    	width: 100%;
			background: #fff;
			border: 1px solid #6cd;
			position: absolute;
			top:3rem;
			color: #000;
			overflow: hidden;
			display: none;
			li{
				width: 100%;
				height:2.9rem;
				line-height:2.9rem;
				font-size:1.4rem;
				color: #000;
				padding-left:0.4rem;
				&.option-active{
					color: #6cd;
					background: #333;
				}
			}
	    }
    }
}

.goto-top {
  width:3rem;
  height:3rem;
  position: fixed;
  z-index: 1000;
  top: 75%;
  right: 6%;
  display: none;
}

.goto-top img {
    display:block;
  width:3rem;
}





